import React from 'react';
import { Text, ResponsiveContext } from 'grommet'
import styled from 'styled-components'
import { useTranslation } from 'react-i18next';
import { Symbol } from './Styles';

const QiMen = ({ index, ...props }) => {
    const cardinal = ["SE", "S", "SW", "E", "", "W", "NE", "N", "NW"]
    const { t } = useTranslation()
    const size = React.useContext(ResponsiveContext);

    return <>
        <Symbol index={0}>
            <Text color="white" size={size}>{ props.heaven }</Text>
            <Text color="white" size="10px"></Text>
        </Symbol>
        <Symbol index={1}>
            <Text color="white" size={size}>{ props.star }</Text>
            <Text color="white" size="10px">{ t(props.star) }</Text>
        </Symbol>
        <Symbol index={2}>
            <Text color="white" size={size}>{ props.god }</Text>
            <Text color="white" size="10px">{ t(props.god) }</Text>
        </Symbol>
        {
            index === 4 && <Symbol index={4}>
            <Text color="white" size={size}>
                { props.structure.replace('F', 'Yin').replace('M', 'Yang') }
            </Text>
            
            </Symbol>
        }

        <Symbol index={6}>
            <Text color="white" size={size}>{ props.earth }</Text>
            <Text color="white" size="10px"></Text>
            {
                index === 4 && <Text size="14px" color="white">openfengshui.com</Text>
            }
        </Symbol>
        <Symbol index={7}>
            <Text color="white" size={size}>{ props.door }</Text>
            <Text color="white" size="10px">{ t(props.door) }</Text>
        </Symbol>
        <Symbol index={8}>
            <Text color="white" size={size}>{ cardinal[index] }</Text>
        </Symbol>
        </>
    ;
}

export default QiMen;