import React, { useState, useEffect } from 'react';
import { TextInput, DateInput, Box, Select, Button  } from 'grommet';
import { parseISO, format, setMinutes, setHours, setYear, setMonth, setDate as setDateObjectDate } from 'date-fns';

const DateTimePicker = ({ dateObject, selectDate, onClick }) => {
    const [useDateObject, setDateObject] = useState(dateObject || new Date())
    
    const [year, setYearText] = useState(format(useDateObject, 'yyyy'));
    const [month, setMonthText] = useState(format(useDateObject, 'M'));
    const [day, setDayText] = useState(format(useDateObject, 'd'));
    const [date, setDate] = useState(useDateObject.toISOString());
    const [hour, setHour] = useState(format(useDateObject, 'H'))
    const [min, setMin] = useState(format(useDateObject, 'm'))

    useEffect(() => {
        let dateObj = parseISO(date);
        dateObj = setYear(dateObj, year)
        dateObj = setMonth(dateObj, month - 1)
        dateObj = setDateObjectDate(dateObj, day)
        dateObj = setHours(dateObj, hour)
        dateObj = setMinutes(dateObj, min)
        selectDate && selectDate(dateObj)
        setDateObject(dateObj)
    }, [hour, min, date, year, month, day])

    return <><Box
            direction="row-responsive"
            pad={{
                bottom: "small"
            }}
            >

            <Select
                options={[...Array(125).keys()].map(e => `${e + 1902}`).reverse()}
                value={year}
                onChange={({ option }) => setYearText(option)}
            />

            <Select
                options={[...Array(12).keys()].map(e => `${e + 1}`)}
                value={month}
                onChange={({ option }) => setMonthText(option)}
            />

            <Select
                options={[...Array(31).keys()].map(e => `${e + 1}`)}
                value={day}
                onChange={({ option }) => setDayText(option)}
            />


        </Box>
        <Box
            direction="row-responsive"
            pad={{
                bottom: "small"
            }}
        >

            <Select
                options={Object.keys([...Array(24).keys()])}
                value={hour}
                onChange={({ option }) => setHour(option)}
            />

            <Select
                options={Object.keys([...Array(60).keys()])}
                value={min}
                onChange={({ option }) => setMin(option)}
            />
        </Box>
        <Box align="end">
            {
                onClick && <Button 
                onClick={() => onClick && onClick(useDateObject)}
                label="Get Chart"
                />
            }

        </Box>
    </>;
}

export default DateTimePicker;