import React, { useState, useEffect } from "react"
import { Layer, List, Select, Box, Button } from 'grommet';
import DateTimePicker from '../components/DateTimePicker'
import { formation } from '../cm-lib/reference/Formation'
import { DIRECTION } from '../cm-lib/constants'
import Bazi from '../components/Bazi'
import QiMenChart from '../components/QiMen'
import { useTranslation } from 'react-i18next';

const SearchFormation = ({ handleSearch }) => {
    const { t } = useTranslation();
    const [selectedFormation, setSelectedFormation] = useState('');
    const [showList, setShowList] = useState(false)
    const [searchList, setSearchList] = useState([])
    return <Box direction="row">
        <Box direction="row-responsive">
        <Select
            options={Object.keys(formation).map(k => ({ 
                label: `${k} ${t(k)}`,
                value: k
            }))}
            placeholder="Formation"
            labelKey="label"
            valueKey={{ key: 'value', reduce: true  }}
            value={selectedFormation}
            onChange={({ value: nextValue }) => setSelectedFormation(nextValue) }
        />
        <Button 
            disabled={!selectedFormation}
            primary label="Search" onClick={() => {
            setSearchList(handleSearch(selectedFormation, DIRECTION))
            setShowList(true)
        }

        }/>

        </Box>
        {
            showList && <Layer 
                display={showList}
                onClickOutside={() => setShowList(false)}
            >
                <List
                primaryKey="display"
                secondaryKey="percent"
                data={searchList}
                />
                <Button label="close" onClick={() => setShowList(false)} />
            </Layer>
        }
    </Box>
}

const Home = ({ onClick, chart, initialDate, onDateChanged, date, handleSearch, onSquarePressed }) => {
  const [selectedDate, setSelectedDate] = useState(date || new Date())
  const [qimenChart, setQimenChart] = useState({})
  useEffect(() => {
    onDateChanged && onDateChanged(selectedDate, setQimenChart)
  }, [selectedDate])

  return <>
    <DateTimePicker onClick={onClick} dateObject={initialDate} selectDate={setSelectedDate} />
    {
        handleSearch && <SearchFormation handleSearch={handleSearch} />
    }
    <br />
    {
        !initialDate && <Bazi inputDate={selectedDate} />
    }
    {
        initialDate && <Bazi inputDate={initialDate} />
    }
    <br />
    <QiMenChart chart={chart || qimenChart} onSquarePressed={onSquarePressed} />
  </>
}

export default Home
