import React from "react"
import { Box, base } from 'grommet';
import styled from 'styled-components'
import Square from '../Square'
import QiMen from '../Square/QiMen'
import { withDirection } from '../../helpers'

const BoxWrapper = styled(Box)`
  width: 32%;
  margin: 2px;
`

const Home = ({ chart, onClick, onSquarePressed }) => {
  return <>
  <Box 
    alignSelf="center"
    justify="center"
    direction="row"
    wrap={true}
    fill="horizontal"
  >
  {
    chart && chart.chart && withDirection(chart.chart).map((e, i) => <BoxWrapper
      border={true}
    >
    <Square 
        direction={e.direction}
        onSquarePressed={onSquarePressed}
        data={{
          structure: chart.structure,
          ...e,
          index: i,
        }} 
        Template={QiMen} />
    </BoxWrapper>)
  }
  </Box>
  </>
}

export default Home
